@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color : #F7F7FA;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.login_box{
  width : 1000px;
  height : 700px;
  background-color : white;
  
}

.color_bg{
  background-color: #3D7CE2;
}

.color_bg2{
  background-color: #B0A0FC;
}

.color_text{
  color: #0064FF;
}
.color_text2{
  color: #7E7E7E;
}


.menu:hover{
  background-color : #D9EEFF;
}

.select_menu{
  background-color : #D9EEFF;
  border-color: #0064FF;
}

.border-1{
  border-width: 1px;
}

.h-580{
  height: 580px;
}

@media (max-width: 1740px) {
  .width100_1740px{
    width : 100%
  }
}

@media (max-width: 1300px) {
  .width50_1300px{
    width : 50%
  }
  .width100_1300px{
    width : 100%
  }
  .del_1300px {
    display: none;
  }
}

@media (max-width: 1000px) {
  .width100_1000px{
    width : 100%
  }

  .del_1000px {
    display: none;
  }
}

@media (max-width: 950px) {
  .del_950px {
    display: none;
  }

  .justify_950px {
    justify-content: unset;
  }

  .menu_950px {
    position: fixed;
    height: 100%;
  }

}
@media (max-width: 720px) {
  .del_720px {
    display: none;
  }

  .width100_720px{
    width : 100%
  }

  .width20_720px{
    width: 20px;
  }

  .height100_720px{
    height : 100%
  }

  .mb_720px{
    margin-bottom: 20px;
  }

  
  
}

@media (max-width: 550px) {
  .box_550px {
    flex-direction: column;
  }

  .height100_550px{
    height : 100%
  }

  .height160px_550px{
    height : 160px
  }

  .width100_550px{
    width : 100%
  }

  .margin_550px{
    margin: unset;
  }


}

@media (max-width: 400px) {
  .del_400px {
    display: none;
  }
}